  border-radius: 12px;
  box-shadow: 3px 7px 7.5px 0px rgba(0, 0, 0, 0.1490196078);
}
.block--u.quotes-light .quote-carousel .slide {
  background-color: #fff;
}
.block--u.quotes-light .quote-carousel .slide .person-img-wrap .star--rating {
  display: none;
}
.block--u.quotes-light .quote-carousel .lSPager {
  display: none;
}
.block--u.quotes-light .quote-carousel .slide .txt-content {
  color: #002ea6;
}
